@import url('https://fonts.googleapis.com/css2?family=Signika:wght@300;400;600;700&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Oxygen:wght@300;400;700&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Poppins&display=swap');

$mainFont: Signika;
$secondFont: Oxygen;
$thirdFont: Poppins;

$mainColor: rgb(5, 41, 77);
$secondaryColor: rgb(3, 40, 77);

$borderRadius: 0.25vh;

$gridCols: 5;
$gridRows: 5;
$gridSize: 10.2vh;
$gridGap: 2px;
$containerSize: calc(#{$gridRows} * #{$gridSize + 0.22vh} + #{$gridRows} * #{$gridGap});
$imageSize: $gridSize - 2.5vh;

$textShadow: 0.1vh 0.1vh 0 rgba(0, 0, 0, 0.7);

* {
  font-family: $mainFont;
  font-size: 1.5vh;
  font-weight: 400;
  box-sizing: content-box;
  user-select: none;
  margin: 0;
  color: #fff;
}

body,
html {
  overflow: hidden;
  background:url(https://i.imgur.com/crMFDLq.png)
}

img {
  image-rendering: -webkit-optimize-contrast;
  max-width: $imageSize;
  max-height: $imageSize;
}

.center-wrapper {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100vh;
  background:url(https://i.imgur.com/crMFDLq.png)
}

.column-wrapper {
  display: flex;
  flex-direction: column;
}

.row-wrapper {
  display: flex;
  flex-direction: row;
}

.inventory-label {
  display: flex;
  justify-content: space-between;
  margin-bottom: 0.5vh;
}

.inventory-grid {
  display: grid;
  height: $containerSize;
  grid-template-columns: repeat($gridCols, $gridSize);
  grid-auto-rows: $gridSize + 0.22vh;
  gap: $gridGap;
  overflow-y: scroll;
  margin-top: 0.5vh;
  margin-bottom: 1vh;
}

.drag {
  width: $imageSize;
  height: $imageSize;
  z-index: 1;
  position: fixed;
  pointer-events: none;
  top: 0;
  left: 0;
  background-repeat: no-repeat;
  background-position: center;
  background-size: $imageSize;
  image-rendering: -webkit-optimize-contrast;
}

@for $i from 1 through 5 {
  .inventory-grid-player > .item-container:nth-child(#{$i}) > .item-count:before,
  .hotbar-grid > .item-container:nth-child(#{$i}) > .item-count:before {
    content: '#{$i}';
    width: 1.5vh;
    height: 1.7vh;
    line-height: 1.7vh;
    font-size: 0.7em;
    border-top-left-radius: $borderRadius;
    border-bottom-right-radius: $borderRadius;
    font-family: $secondFont;
    font-weight: 700;
    text-align: center;
    background-color: white;
    color: black;
  }
}

.item-container {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  background-color: rgba(0, 0, 0, 0.385);
  background-repeat: no-repeat;
  background-position: center;
  background-size: $imageSize;
  image-rendering: -webkit-optimize-contrast;
  position: relative;

  .item-count {
    display: flex;

    span {
      font-family: $secondFont;
      margin-left: auto;
      font-size: 0.7em;
      font-weight: 300;
      padding: 0.2em;
      text-shadow: $textShadow;
    }
  }

  .item-durability {
    margin-top: auto;
    background: rgba(0, 0, 0, 0.5);
    height: 0.3em;
    overflow: hidden;

    div {
      height: 100%;
    }
  }

  .item-label {
    background-color: $mainColor;
    padding: 0.1em;
    margin: -1px;
    font-size: 0.7em;
    text-align: center;
    text-transform: uppercase;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    border-bottom-left-radius: inherit;
    border-bottom-right-radius: inherit;
    border: inherit;
    border-top: 1px inset rgba(0, 0, 0, 0.4);
  }

  .item-price {
    text-shadow: $textShadow;
    position: absolute;
    font-size: 0.8em;
    bottom: 1.4vh;
    right: 0.1vh;
    height: 2vh;
    display: flex;
    justify-content: center;
    align-items: center;

    .item-currency {
      height: auto;
      width: 2vh;
      backface-visibility: hidden;
      -ms-transform: translateZ(0); /* IE 9 */
      -webkit-transform: translateZ(0); /* Chrome, Safari, Opera */
      transform: translateZ(0);
    }
  }
}

.button3,input[type=number]{
  position:relative;
  background-color:rgba(4,42,80,.344);
  padding:.9vh;
  text-align:center;
  width:7vh;
  margin:1vh;
  top:0vh;
  left:-53.6vh
}

.button {
  background-color: rgba(12, 12, 12, 0.4);
  border: 1px solid rgba(0, 0, 0, 0.15);
  border-radius: $borderRadius;
  padding: 0.9vh;
  text-align: center;
  width: 5vh;
  margin: 0.8vh;

  &:hover {
    background-color: $mainColor;
    opacity: 0.8;
  }
}

.misc-btns {
  position: absolute;
  bottom: 1vh;
  right: 1vh;
  display: flex;
  flex-direction: row;
  justify-content: space-between;

  button {
    @extend .button;
    width: 2vh;
    font-size: 2.5vh;
    display: flex;
    justify-content: center;
    align-items: center;
    background: none;
    border: none;
  }
}

input[type='number'] {
  @extend .button;
  margin-bottom: 3vh !important;

  &::-webkit-inner-spin-button,
  &::-webkit-outer-spin-button {
    -webkit-appearance: none;
  }

  &:focus {
    outline: none;
  }
}

::-webkit-scrollbar {
  display: none;
}

.weight-bar {
  background: rgba(0, 0, 0, 0.4);
  border: 1px inset rgba(0, 0, 0, 0.1);
  height: 0.7em;
  border-radius: $borderRadius;
  overflow: hidden;

  div {
    height: 100%;
  }
}

.hotbar-grid {
  display: grid;
  grid-template-columns: repeat(5, $gridSize);
  grid-auto-rows: $gridSize;
  gap: 2px;
  position: absolute;
  bottom: 2vh;
  border-radius: $borderRadius;

  .item-container {
    border: 1px inset rgba(0, 0, 0, 0.3);
  }
}

.item-info {
  padding: 0.7vh !important;
  font-size: 0.95em !important;
  background-color: $mainColor !important;
  opacity: 1 !important;
  border-radius: $borderRadius !important;
  width: 23vh;
  min-width: 180px;
  text-shadow: $textShadow;
  border: 1px solid rgba(0, 0, 0, 0.2) !important;
  line-height: 1.1em !important;

  p {
    font-size: 0.95em !important;
  }

  strong {
    font-size: 0.95em !important;
    font-weight: 600 !important;
  }
}

.progressBar {
  width: 28vh;
  height: 3.6vh;
  position: absolute;
  bottom: 13vh;
  left: 50%;
  transform: translate(-50%, 0);
  background-color: rgba(0, 0, 0, 0.6);
  border-radius: $borderRadius;

  &-value {
    width: 0;
    height: 100%;
    background-color: rgb(52, 152, 219);
    border-radius: inherit;
    animation: progressBar linear forwards;

    span {
      position: absolute;
      top: 50%;
      left: 50%;
      font-size: 1.25em;
      width: 100%;
      text-align: center;
      transform: translate(-50%, -50%);
      overflow: hidden;
      display: inline-block;
      text-overflow: ellipsis;
      white-space: nowrap;
    }

    @keyframes progressBar {
      from {
        width: 0;
      }
      to {
        width: 100%;
      }
    }
  }

  &-cancel {
    @extend .progressBar-value;
    animation-play-state: paused;
    width: 100% !important;
    background-color: rgb(198, 40, 40);
  }
}

.info-fade {
  z-index: 99;
}

.info-main {
  position: absolute;
  width: 30vw;
  height: fit-content;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  background-color: $mainColor;
  border-radius: $borderRadius;
  z-index: 99;

  h2 {
    font-size: 3vh;
    text-align: center;
    padding-bottom: 2vh;
    padding-top: 2vh;
  }

  p {
    font-size: 1.8vh;
    padding: 2vh;
  }

  .info-exit-icon {
    font-size: 2.8vh;
    position: absolute;
    right: 1vh;
    top: 1vh;
  }

  .info-ox {
    position: absolute;
    right: 1vh;
    bottom: 1vh;
  }
}

.keyboard-container {
  height: fit-content;
  z-index: 999;
  width: 30vh;
  background-color: $mainColor;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  border-radius: $borderRadius;
  text-shadow: $textShadow;

  .keyboard-header {
    text-shadow: none;
    font-size: 2vh;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 0.5vh;
    letter-spacing: 0.08vh;
    border-bottom: 0.3vh solid grey;
  }

  .keyboard-component {
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding: 1.7vh;
    padding-top: 0.6vh;
    padding-bottom: 0.5vh;
    align-items: center;
    p {
      margin-left: 0.4vh;
      font-size: 1.6vh;
      align-self: flex-start;
    }
    input {
      border-radius: $borderRadius;
      background-color: $secondaryColor;
      height: 2.4vh;
      width: 25vh;
      font-size: 1.5vh;
      padding: 0.4vh;
      border: none;
      outline: none;
    }
  }

  .keyboard-buttons-div {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 1vh;

    button {
      border: none;
      outline: none;
      font-size: 1.6vh;
      padding: 0.6vh;
      border-radius: $borderRadius;
      background-color: #4caf50;
    }
  }
}

.react-contexify {
  border-radius: $borderRadius;
  background-color: $mainColor;
  box-shadow: none;
  border: 1px inset rgba(0, 0, 0, 0.2) !important;
  padding: 3px 0;
}

// Dark Theme ------------------------------------------------------- //
.react-contexify__theme--dark {
  font-size: 1.3vh !important;
  background-color: $mainColor;

  .react-contexify__submenu {
    background-color: $mainColor;
    border-radius: $borderRadius;
    top: -3px;
  }
  .react-contexify__separator {
    background-color: rgba(0, 0, 0, 0.2);
  }
  .react-contexify__item {
    &__content {
      font-size: 1.3vh !important;
      color: #ffffff;
      padding: 3px 6px;
    }
  }
}

.react-contexify__item:not(.react-contexify__item--disabled):hover
  > .react-contexify__item__content,
.react-contexify__item:not(.react-contexify__item--disabled):focus
  > .react-contexify__item__content {
  background-color: $secondaryColor;
}

// Item transition stuff
.item-action {
  width: 100%;
  background-color: white;
  font-family: $thirdFont;
  color: black;
  text-align: center;
  font-size: 1.1vh;
  border-top-left-radius: $borderRadius;
  border-top-right-radius: $borderRadius;
}

.item-notifications-container {
  display: flex;
  gap: 2px;
  position: absolute;
  bottom: 20vh;
  left: 50%;
  transform: translateX(-50%);
}

.item-notification {
  @extend .item-container;
  height: calc($imageSize * 1.4);
  width: calc($imageSize * 1.3);
  justify-content: space-between;
  border: 1px inset rgba(0, 0, 0, 0.3);
}

.item-notification-enter {
  opacity: 0;
  transform: scale(0);
}
.item-notification-enter-active {
  opacity: 1;
  transform: scale(1);
  transition: opacity 500ms, transform 500ms ease;
}
.item-notification-exit {
  opacity: 1;
  transform: scale(1);
}
.item-notification-exit-active {
  opacity: 0;
  transform: scale(0);
  transition: opacity 500ms, transform 500ms ease;
}




